body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    background-color: #2196F3;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 140px;
    font-weight: bold;
    color: #2196F3;
    line-height: 68px;
    height: 140px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 140px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: inline-grid;
    grid-template-columns: auto auto auto;
    background-color: #2196F3;
    padding: 10px;
    grid-gap: 10px;
  }

  .game-board {
    margin-left: 20px;
    grid-column: 1;
    grid-row: 2;
    grid-column: 2;
  }

  .TestText {
    margin-left: 20px;
    grid-row: 1;
    grid-column: 2;
    font-size:24px;
  }

  .game-info {
    grid-row: 2;
    grid-column: 3;
    font-size:28px;
    font-weight: bold;
  }

  .Pad2{
    grid-column: 1;
    font-size: 120px;
    color: #2196F3;
  }